import React, { useEffect, useState } from 'react';
import '../all.css';
import Form from 'react-bootstrap/Form';
import Table from 'react-bootstrap/Table';
import { getApi, postApi, deleteApi, updateApi } from './api';
import Button from 'react-bootstrap/Button';
import Alert from 'react-bootstrap/Alert';
import { Spinner, Modal } from 'react-bootstrap';

const Crud = () => {
    const [data, setData] = useState([]);
    const [alert, setAlert] = useState({ show: false, message: "", variant: "danger" });
    const [loading, setLoading] = useState(false);
    const [inputData, setInputData] = useState({
        Nama: "",
        Kelas: "",
        Jurusan: "",
    });
    const [editingData, setEditingData] = useState(null); // Menambahkan state untuk menyimpan data yang sedang diedit
    const [showModal, setShowModal] = useState(false);
    const [itemToDelete, setItemToDelete] = useState(null);
    useEffect(() => {
        fetchApiData();
    }, []);

    const fetchApiData = () => {
        getApi()
            .then(res => {
                setData(res);
            })
            .catch(error => {
                console.error('Error:', error);
            });
    };

    useEffect(() => {
        if (loading) {
            setTimeout(() => {
                setLoading(false);
            }, 1000);
        }
    }, [loading]);

    useEffect(() => {
        let alertTimeout;

        if (alert.show) {
            alertTimeout = setTimeout(() => {
                setAlert({ show: false, message: "" });
            }, 1000);
        }

        return () => clearTimeout(alertTimeout);
    }, [alert]);

    const submit = (e) => {
        e.preventDefault();
        setLoading(true);
        if (editingData) {
            // Jika sedang dalam mode edit, lakukan pembaruan data
            if (inputData.Nama.trim() === "" || inputData.Kelas.trim() === "" || inputData.Jurusan.trim() === "") {
                setAlert({ show: true, message: "Harap isi semua kolom", variant: "danger" });
                setLoading(false);
                return;
            }
            if (inputData.Nama !== editingData.Nama && data.some(item => item.Nama === inputData.Nama)) {
                setAlert({ show: true, message: "Nama ini sudah digunakan", variant: "danger" });
                setLoading(false);
                return;
            }
            handleUpdate(editingData.id, inputData);
        } else {
            // Jika tidak, tambahkan data baru
            if (inputData.Nama.trim() === "" || inputData.Kelas.trim() === "" || inputData.Jurusan.trim() === "") {
                setAlert({ show: true, message: "Harap isi semua kolom", variant: "danger" });
                setLoading(false);
                return;
            }

            const duplicate = data.some(item => item.Nama === inputData.Nama);
            if (!duplicate) {
                postApi(inputData)
                    .then(() => {
                        fetchApiData();
                        setInputData({
                            Nama: "",
                            Kelas: "",
                            Jurusan: ""
                        });
                        setAlert({ show: true, message: "Data berhasil dikirim", variant: "success" });
                        setLoading(false);
                    })
                    .catch(error => {
                        console.error('Error:', error);
                        setLoading(false);
                    });
            } else {
                setAlert({ show: true, message: "Nama ini sudah digunakan", variant: "danger" });
                setLoading(false);
            }
        }
    };

    const handleDelete = (id) => {
        setItemToDelete(id);
        setShowModal(true);
    }

    const handleConfirmDelete = async () => {
        setLoading(true);
        try {
            await deleteApi(itemToDelete);
            const updatedData = data.filter(item => item.id !== itemToDelete);
            setData(updatedData);
            setAlert({ show: true, message: "Data berhasil dihapus", variant: "danger" });
        } catch (error) {
            console.error('Error:', error);
        } finally {
            setLoading(false);
            setShowModal(false); // Tutup modal setelah selesai
        }
    }

    const handleInputChange = (e) => {
        setInputData({ ...inputData, [e.target.id]: e.target.value });
    };

    const handleEdit = (id) => {
        const editItem = data.find(item => item.id === id);
        setEditingData(editItem);
        setInputData(editItem);
    };

    const handleUpdate = async (id, updatedData) => {
        setLoading(true);
        try {
            await updateApi(id, updatedData);
            const updatedList = data.map(item => {
                if (item.id === id) {
                    return { ...item, ...updatedData };
                }
                return item;
            });
            setData(updatedList);
            setAlert({ show: true, message: "Data berhasil diperbarui", variant: "success" });
            // Reset inputData to default values after update is completed
            setInputData({
                Nama: "",
                Kelas: "",
                Jurusan: ""
            });
        } catch (error) {
            console.error('Error:', error);
        } finally {
            setLoading(false);
            setEditingData(null);
        }
    };

    return (
        <>
            {loading && (
                <div className="blurrr">
                    <div className="loadCont">
                        <Spinner animation="border" variant="primary" style={{ width: '150px', height: '150px' }} />
                    </div>
                </div>
            )}
            <div className='container-fluid p-5'>
                {alert.show && (
                    <div className='d-flex justify-content-center position-absolute w-100 aler'>
                        <Alert style={{ width: "310px" }} variant={alert.variant} dismissible>{alert.message}</Alert>
                    </div>
                )}
                <div className='text-center'>
                    <h1>Data Siswa SMK Letris 2</h1>
                </div>
                <div>
                    <form className='d-flex gap-5 text-center p-5' onSubmit={(e) => submit(e)}>
                        <input onChange={(e) => handleInputChange(e)} value={inputData.Nama} id="Nama" className='w-50' type="text" placeholder="Tulis nama dimarih" />
                        <Form.Select onChange={(e) => handleInputChange(e)} value={inputData.Kelas} id="Kelas" className='w-50' aria-label="Default select example">
                            <option>Tentukan Kelas</option>
                            <option value="X">X</option>
                            <option value="XI">XI</option>
                            <option value="XII">XII</option>
                            <option value="XIII">XIII</option>
                            <option value="XIV">XIV</option>
                            <option value="XV">XV</option>
                        </Form.Select>
                        <Form.Select onChange={(e) => handleInputChange(e)} value={inputData.Jurusan} id="Jurusan" className='w-50' aria-label="Default select example">
                            <option>Pilih Jurusan</option>
                            <option value="Multimedia">Multimedia</option>
                            <option value="Perbankan Syariah">Perbankan syariah</option>
                            <option value="Teknik Komputer Jaringan">Teknik komputer jaringan</option>
                            <option value="Rekayasa Perangkat Lunak">Rekayasa perangkat lunak</option>
                            <option value="Akuntansi">Akuntansi</option>
                            <option value="Tata Boga">Tata boga</option>
                        </Form.Select>
                        <button className='w-50'>{editingData ? "Update" : "Simpan"}</button>
                    </form>
                </div>
                <div className="p-5">
                    <Table striped bordered className='text-center'>
                        <thead>
                            <tr>
                                <th className='col-1'>No</th>
                                <th className='col-3'>Nama lengkap</th>
                                <th className='col-2'>Kelas</th>
                                <th className='col-2'>Jurusan</th>
                                <th className='col-2'>Action</th>
                            </tr>
                        </thead>
                        <tbody>
                {data && data.map((item, index) => (
                    <tr key={index}>
                        <td>{index + 1}</td>
                        <td>{item.Nama}</td>
                        <td>{item.Kelas}</td>
                        <td>{item.Jurusan}</td>
                        <td>
                            <div className="w-100 gap-3 rounded d-flex justify-content-center">
                                <Button variant="success" className='col-4 rounded-3 border border-none' onClick={() => handleEdit(item.id)}>Edit</Button>
                                <Button variant="danger" onClick={() => handleDelete(item.id)} className='col-4 rounded-3 border border-none'>Delete</Button>
                            </div>
                        </td>
                    </tr>
                ))}
            </tbody>

            <Modal show={showModal} onHide={() => setShowModal(false)}>
                <Modal.Header closeButton>
                    <Modal.Title>Konfirmasi Penghapusan</Modal.Title>
                </Modal.Header>
                <Modal.Body>Apakah Anda yakin ingin menghapus data ini?</Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={() => setShowModal(false)}>
                        Batal
                    </Button>
                    <Button variant="danger" onClick={handleConfirmDelete}>
                        Ya
                    </Button>
                </Modal.Footer>
            </Modal>
                    </Table>
                </div>
            </div>
        </>
    );
};

export default Crud;
