import axios from 'axios'

export const postApi = async(dataUser) => {
    return await axios.post("https://65d2e0f0522627d5010787ea.mockapi.io/Murid",dataUser)
      .then(res => res)
      .catch(error => {
        console.error('Error:', error); // Handle errors
    });
}

export const getApi = async () => {
    try {
        const response = await axios.get("https://65d2e0f0522627d5010787ea.mockapi.io/Murid");
        return response.data; // Mengembalikan data yang diterima dari API
    } catch (error) {
        console.error('Error:', error); // Tangani kesalahan
        throw error; // Diteruskan untuk menangani kesalahan di tempat panggilan
    }
}

export const deleteApi = async (id) => {
    const response = await axios.delete(`https://65d2e0f0522627d5010787ea.mockapi.io/Murid/${id}`);
    return response.data;
}

export const updateApi = async (id, updatedData) => {
    return await axios.put(`https://65d2e0f0522627d5010787ea.mockapi.io/Murid/${id}`, updatedData)
        .then(res => res)
        .catch(error => {
            console.error('Error:', error);
            throw error;
        });
};
